.Card {
    position: relative;
    margin: 10px auto;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    border-radius: 5px;
    padding: 10px;
    height: 50%;
    max-width: 500px;
    color: whitesmoke;
    text-align: left;
}

.Card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.5);
    cursor: pointer;
}

h2 {
    left: 5px;
    bottom: 5px;
    margin: 0;
    position: absolute;
}